html, body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}



header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 2rem;
  padding: 0 1rem;
}

#header-logo {
  height: 1.5rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.menu {
  margin: 0;
}

.menu-item {
  display: inline-block;
}

.menu-item a {
  display: inline-block;
  padding: 25px 25px;
  color: #fff;
  font-weight: bold;
  font-size: 1.2rem;
  -webkit-transition: .2s;
  transition: .2s;
  text-decoration: none;
}

.menu-item a:hover {
  color: #DF600E;
}

#slider {
  position: relative;
  height: 100vh;
  width: 100vw;
  background-color: #1b1b1b;
}

#slider .background {
  height: 100%;
  width: 100%;
}

#slider .slide {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  -webkit-transition: opacity 1.2s ease-in-out;
  transition: opacity 1.2s ease-in-out;
}

#slider .slide.show {
  opacity: 1;
}

#slider .content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 15% 20%;
  background-color: rgba(0,0,0,.5);
}

.cnt-raw {
  margin: 4rem 0;
}

.slogan {
  margin: 0;
  font-size: 4rem;
  font-weight: bold;
  color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.video-btn {
  position: relative;
  margin: 0;
  padding: 10px 20px;
  border: 3px solid #fff;
  border-radius: 5px;
  background-color: transparent;
  color: #fff;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: .2s ease;
  transition: .2s ease;
}

.video-btn:hover {
  background-color: #fff;
  color: #1b1b1b;
}

.video-pop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000;
  padding: 60px 30px;
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
  -webkit-transition: opacity .2s, -webkit-transform 0s .3s;
  transition: opacity .2s, -webkit-transform 0s .3s;
  transition: opacity .2s, transform 0s .3s;
  transition: opacity .2s, transform 0s .3s, -webkit-transform 0s .3s; 
}
.video-pop.open {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;  
}

.close-btn {
  position: fixed;
  top: 20px;
  right: 30px;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
}

.close-btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  height: 3px;
  width: 80%;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
  background-color: #fff;
}

.close-btn::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  height: 3px;
  width: 80%;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
  background-color: #fff;
}

.close-btn:hover::after, .close-btn:hover::before {
  background-color: #aaa;  
}

.pop-inner {
  height: 100%;
  width: 100%;
}
